"use client";

export function LoadingSpinner({ className = "h-5 w-5" }: { className?: string }) {
  return (
    <div className={`animate-spin rounded-full border-2 border-current border-t-transparent ${className}`} />
  );
}

export function LoadingPage() {
  return (
    <div className="min-h-screen flex items-center justify-center">
      <LoadingSpinner className="h-8 w-8" />
    </div>
  );
}

export function Loading() {
  return (
    <div className="flex justify-center p-4">
      <LoadingSpinner className="h-8 w-8" />
    </div>
  );
}

export function LoadingOverlay() {
  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white p-4 rounded-lg shadow-lg">
        <LoadingSpinner className="h-8 w-8" />
      </div>
    </div>
  );
}

export function LoadingButton({ className = "h-4 w-4 mr-2" }: { className?: string }) {
  return <LoadingSpinner className={className} />;
}
